.home__container{
    row-gap: 0.2rem;
}
.home__content{
    grid-template-columns: 116px repeat(2,3fr);
    padding: 3rem;
    column-gap:1rem;
    align-items: center;

}
.home__social{
    display: grid;
    grid-template-columns: max-content;
    row-gap: 1rem;
}
.home__social-icon{
    font-size: 1.25rem;
    color: var(--title-color);

}
.home__social-icon:hover{
    color:var(--title-color-dark)
}
.home__title{
    font-size: var(--big-font-size);
    margin-bottom: var(--mb-0-25);
}
.home__hand{
    width: 38px;
    height: 38px;
    margin-left: 0.4rem;
}
.home__subtitle{
    position: relative;
    font-size:var(--h3-font-size);
    padding-bottom: 1.2rem;
    font-weight: var(--font-normal);
    margin-bottom:var(--mb-1)
}
.home__subtitle::before{
    content: '';
    position: absolute;
    width: 70px;
    height: 1px;
    background-color: var(--text-color);

    left: 0;
    top: 2rem;
}
.home__description{

padding-bottom: 1.5rem;
}
.home__img{
    background: url(C:\Users\SOURAV\Desktop\PORT\portfolio\src\assets\profile1.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    box-shadow: inset 0 0 0 8px rgb(238, 224, 203);
    order: 1;
    justify-self: center;
    width: 300px;
    height:300px;
    animation:profile__animate 8s ease-in-out infinite 1s;
}
@keyframes profile__animate {
    0%{
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }
    50%{
        border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
    }
    100%{
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }
    
}
.home__scroll{
    margin-left: 12rem;
}
.wheel{
    animation: scroll 2s ease infinite;
}
@keyframes scroll {
    0%{
        transform: translateY(0);
    }
    30%{
        transform: translateY(3.75rem);
    }
    
}

.home__scroll-name{
    color:var(--title-color);
    font-weight: var(--font-medium);
    margin-right: var(--mb-0-25);
    margin-left: 2px;
}
.home__scroll-arrow{
    font-size: 1.25rem ;
    color: var(--title-color);
}
/*=============== BREAKPOINTS ===============*/
/* For large devices */
@media screen and (max-width: 992px) {
    .home__content{
        grid-template-columns: 100px repeat(2,1fr);
        column-gap: 1.25rem;
    }
    .home__img{
        width: 250px;
        height: 250px;
        box-shadow: inset 0 0 0 8px rgb(238, 224, 203);
    }
    .home__scroll{
        margin-left:10.4rem;
    }
  }
  
  /* For medium devices */
  @media screen and (max-width: 768px) {
  .home__content{
    grid-template-columns: 0.5fr 3fr;
    padding: top 3.5rme;
  }
  .home__img{
    order:initial;
    justify-self: initial;
  }
  .home__data{
    grid-column: 1/3;
  }
  .home__img{
    box-shadow: inset 0 0 0 6px rgb(238, 224, 203);
    height: 200px;
    width: 200px;
  }
  .home__scroll{
    display: none;
  }
  }
  
 
  
  /* For small devices */
  @media screen and (max-width: 350px) {
    .home__img{
        width: 180px;
        height: 180px;
    }
    
  }
  